/* src/NewYear.css */

/* New Year Intro Animation */
.ny-intro-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: nyFadeOut 1s ease-out 3s forwards;
}

.ny-intro-content {
  display: flex;
  align-items: center;
  color: #ffd700; /* Gold */
  text-shadow: 0 0 15px #ffd700, 0 0 20px #fff;
}

.ny-intro-text {
  font-size: 4rem;
  font-weight: bold;
  font-family: "Mountains of Christmas", cursive; /* Reusing festive font */
  animation: nyZoomIn 2s ease-in-out forwards;
  opacity: 0;
}

.ny-intro-icon {
  font-size: 3rem;
  margin: 0 20px;
  animation: nyPopIn 1s ease-out 1s forwards;
  opacity: 0;
  transform: scale(0);
}

@keyframes nyZoomIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes nyPopIn {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes nyFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}

/* New Year Ringing Bell Effect */
@keyframes ring-bell-animation {
  0% {
    opacity: 0.8;
    transform: translateY(-100%) scale(0.8) rotate(0deg);
  }
  20% {
    opacity: 1;
    transform: translateY(0) scale(1) rotate(0deg);
  }
  30%,
  50%,
  70% {
    transform: translateY(0) scale(1) rotate(20deg);
  }
  40%,
  60%,
  80% {
    transform: translateY(0) scale(1) rotate(-20deg);
  }
  90%,
  100% {
    opacity: 0;
    transform: translateY(-100%) scale(0.8) rotate(0deg);
  }
}

.ringing-bell {
  position: fixed;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 5rem;
  z-index: 9999;
  animation: ring-bell-animation 6s ease-in-out 1s forwards; /* Runs once after 1s delay */
  user-select: none;
  cursor: default;
}

/* New Year Fireworks Effect */
.firework {
  position: fixed;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  z-index: 9998; /* Below bells */
  animation: firework-burst 1.5s ease-out infinite;
  opacity: 0;
}

@keyframes firework-burst {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(3);
    opacity: 0;
  }
}
