/* src/Christmas.css */

@keyframes fall {
  0% {
    opacity: 0;
    top: -10%;
    transform: translateX(20px);
  }
  10% {
    opacity: 1;
  }
  100% {
    top: 100%;
    transform: translateX(-20px);
  }
}

.snowflake {
  position: fixed;
  top: -10%;
  z-index: 9999;
  color: #fff;
  font-size: 1em;
  font-family: Arial, sans-serif;
  text-shadow: 0 0 5px #000;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;

  -webkit-animation-name: fall;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;

  animation-name: fall;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.snowflake.f-1 {
  font-size: 18px;
}
.snowflake.f-2 {
  font-size: 24px;
}
.snowflake.f-3 {
  font-size: 32px;
}
.snowflake.f-4 {
  font-size: 40px;
}

.snow-pile {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: white;
  box-shadow: 0 -5px 15px rgba(255, 255, 255, 0.3);
  transition: height 5s ease-in-out;
  z-index: 9998; /* Below snowflakes */
}

/* Intro Animation */
.intro-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeOut 1s ease-out 3s forwards;
}

.intro-content {
  display: flex;
  align-items: center;
  color: white;
  text-shadow: 0 0 15px #ff0000, 0 0 20px #ff0000;
}

.intro-text {
  font-size: 4rem;
  font-weight: bold;
  font-family: "Mountains of Christmas", cursive; /* A festive font */
  animation: zoomIn 2s ease-in-out forwards;
  opacity: 0;
}

.intro-icon {
  font-size: 3rem;
  margin: 0 20px;
  animation: popIn 1s ease-out 1s forwards;
  opacity: 0;
  transform: scale(0);
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes popIn {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
